import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';

export default class Button extends PureComponent {
  render() {
    const { title, onClick, pushed, className, pushedClassName } = this.props;

    const pushedClass = pushed ? pushedClassName : '';

    return (
      <button
        className={`${className} ${pushedClass}`}
        onClick={onClick}
      >
        <p>{title}</p>
      </button>
    );
  }
}

Button.propTypes = {
  onClick: PropTypes.func.isRequired,
  pushed: PropTypes.bool,
  title: PropTypes.string,
  className: PropTypes.string,
  pushedClassName: PropTypes.string,
};

Button.defaultProps = {
  pushed: false,
  title: 'Click me',
  className: '',
  pushedClassName: '',
};


