import * as actions from '../constants/ActionTypes';

const initialState = {
  day: {
    pushedButton: false,
    dayText: '',
  },
  month: {
    pushedButton: false,
    monthText: '',
  },
  year: {
    pushedButton: false,
    yearText: '',
  },
};

export default function dateReducer(state = initialState, action) {
  switch (action.type) {
    case actions.SET_DAY:
      return {
        ...state,
        day: {
          pushedButton: !state.day.pushedButton,
          dayText: state.day.pushedButton ? '' : new Date().getDate().toString(),
        }
      };

    case actions.SET_MONTH:
      return {
        ...state,
        month: {
          pushedButton: !state.month.pushedButton,
          monthText: state.month.pushedButton ? '' : new Date().getMonth().toString(),
        }
      };

    case actions.SET_YEAR:
      return {
        ...state,
        year: {
          pushedButton: !state.year.pushedButton,
          yearText: state.year.pushedButton ? '' : new Date().getFullYear().toString(),
        }
      };

    case actions.CLEAR_ALL:
      return initialState;

    default:
      return state;
  }
}



