import React, { Component } from 'react';
import { connect } from 'react-redux';

import * as Actions from '../actions/Actions';
import Button from './button/Button';
import DateView from './date-view/DateView';

import './App.scss';

class App extends Component {
  constructor(props) {
    super(props)

    this.onClick = this.onClick.bind(this);
    this.onClickClear = this.onClickClear.bind(this);
  }

  onClick(datePart) {
    const { dispatch } = this.props;

    switch (datePart) {
      case 'day':
        return () => dispatch(Actions.setDay());

      case 'month':
        return () => dispatch(Actions.setMonth());

      case 'year':
        return () => dispatch(Actions.setYear());

      default:
        break;
    }
  }

  onClickClear() {
    this.props.dispatch(Actions.clearAll());
  }

  render() {
    const { day, month, year } = this.props.state.date;

    const blocks = [
      {
        buttonTitle: 'DAY',
        buttonPushed: day.pushedButton,
        buttonClick: this.onClick('day'),
        dateText: day.dayText,
      },
      {
        buttonTitle: 'MONTH',
        buttonPushed: month.pushedButton,
        buttonClick: this.onClick('month'),
        dateText: month.monthText,
      },
      {
        buttonTitle: 'YEAR',
        buttonPushed: year.pushedButton,
        buttonClick: this.onClick('year'),
        dateText: year.yearText,
      },
    ];

    return (
      <div className="root flex">
        <div className="block-central flex">
          <div className="container-content">
            <div className="section-horizontal">
              {blocks.map(block => {
                return (
                  <div
                    key={block.buttonTitle}
                    className="block"
                  >
                    <Button
                      className="btn container flex"
                      pushedClassName="btn-pushed"
                      onClick={block.buttonClick}
                      pushed={block.buttonPushed}
                      title={block.buttonTitle}
                    />
                    <DateView
                      className="text container flex"
                      text={block.dateText}
                    />
                  </div>
                );
              })}
            </div>
            <button
              className="btn btn-clear"
              onClick={this.onClickClear}
            >
              CLEAR
            </button>
          </div>
        </div>
      </div>
    );
  }
}

export default connect(
  state => {
    return { state };
  },
)(App);




